import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CurrentTierService {
  private currentTierSubject = new BehaviorSubject<any>(null);
  currentTier$ = this.currentTierSubject.asObservable();

  setcurrentTier(currentData: any) {
    this.currentTierSubject.next(currentData);
  }
}
