import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataTierService {
  private dataTierSubject = new BehaviorSubject<any>(null);
  dataTier$ = this.dataTierSubject.asObservable();

  setDataTier(data: any) {
    this.dataTierSubject.next(data);
  }
}
