/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, OnInit } from '@angular/core';
import {
  AccountTypes,
  MembershipService,
  ResponseListPurchasePackageDto,
  TierPrograms,
  TierType,
  UserTierDetail
} from '@youtap-web-lib';
import { ActivatedRoute, Router } from '@angular/router';
import { AppData } from '../../shared/data/app-data';
import { UserTierStatusType } from '../../../../../../libs/youtap-web-lib/src/lib/types/enums.type';
import { DataTierService } from '../../shared/service/data-tier.service';
import { CurrentTierService } from '../../shared/service/current-tier.service';
import { GaService } from '../../shared/service/ga.service';
import { WindowActionService } from '../../shared/service/window-action.service';

interface Items {
  label: string;
  routerLink: string[];
}
@Component({
  selector: 'membership-detail',
  templateUrl: './membership-detail.component.html',
  styleUrls: ['./membership-detail.component.scss']
})
export class MembershipDetailComponent implements OnInit {
  loadingListTierActive = false;
  loadingTiercard = false;
  dataListTierActive!: TierType[];
  dataTierCard!: UserTierDetail;
  tierPrograms!: TierPrograms[];
  listPurchasePackage!: ResponseListPurchasePackageDto;

  btnLoadingLogout = false;
  accountTypes = AccountTypes;
  isChecked = true;
  isExpand = false;
  items: Items[] = [];
  status!: UserTierStatusType;
  tierId = '';
  currentPosition = 0;
  backgroundColor = {
    from: 'rgba(28, 174, 212, 1)',
    to: 'rgba(150, 214, 230, 1)'
  };
  selectedTierData!: TierType | undefined;
  selectedTier!: string;
  position = 0;
  constructor(
    private membershipService: MembershipService,
    private router: Router,
    private route: ActivatedRoute,
    public appData: AppData,
    private dataTierService: DataTierService,
    private currentTierService: CurrentTierService,
    private gaService: GaService,
    private windowActionService: WindowActionService
  ) {}

  ngOnInit() {
    this.getUserTierDetail();
  }

  getListTierActive() {
    this.loadingListTierActive = true;
    this.membershipService.listTierActive().subscribe({
      next: ({ data }) => {
        this.loadingListTierActive = false;
        this.dataListTierActive = data.listTierActive.tiers.filter(
          (item) => item.name !== 'basic'
        );

        if (this.dataListTierActive) {
          this.setInitialDataTier();
          this.defineLockedTier();
          if (this.dataListTierActive) {
            this.initSelectedTierData();
          }
        }
      },
      error: () => {
        this.loadingListTierActive = false;
      }
    });
  }

  defineLockedTier() {
    this.dataListTierActive = this.dataListTierActive.map((item) => {
      if (item.position) {
        this.position = item.position.sequence;
      }

      //Parse backgroundColor
      const backgroundColorParsed = JSON.parse(item.backgroundColor);
      //define locked tier
      const isLockedTier = this.position > this.currentPosition ? true : false;

      const isCurrentTier =
        this.position === this.currentPosition ? true : false;

      return {
        ...item,
        isCurrentTier,
        isLockedTier,
        backgroundColorParsed,
        isActive: false
      };
    });
  }

  initSelectedTierData() {
    let selectedTierItem: TierType | undefined = {} as TierType;
    if (this.dataListTierActive && this.selectedTier) {
      selectedTierItem = this.dataListTierActive.find(
        (item) => item && item.name === this.selectedTier
      );
    }
    this.selectedTierData = selectedTierItem;
  }

  setInitialDataTier() {
    let dataTier = this.dataListTierActive.filter(
      (item) => item.id === this.tierId
    );
    dataTier = dataTier.map((item) => {
      const currentPosition = this.currentPosition;
      return {
        ...item,
        currentPosition
      };
    });

    this.dataTierService.setDataTier(dataTier[0]);
    this.currentTierService.setcurrentTier(dataTier[0]);
  }

  getUserTierDetail() {
    this.loadingTiercard = true;

    this.membershipService.userTierDetail().subscribe({
      next: ({ data }) => {
        this.loadingTiercard = false;
        this.status = data.userTierDetail.status;
        if (data.userTierDetail.tier) {
          this.currentPosition = data.userTierDetail.currentPosition;
          this.tierId = data.userTierDetail.tier.id;
          this.dataTierCard = data.userTierDetail;
          if (
            this.status === UserTierStatusType.NEXT_TIER &&
            data?.userTierDetail?.nextTier !== null
          ) {
            this.tierPrograms = data.userTierDetail.nextTier.tierPrograms;
          }
          if (this.status === UserTierStatusType.IN_PROGRESS) {
            this.tierPrograms = data.userTierDetail.tier.tierPrograms;
          }
          if (
            this.status === UserTierStatusType.NEXT_TIER &&
            data?.userTierDetail?.nextTier === null
          ) {
            this.tierPrograms = data.userTierDetail.tier.tierPrograms;
          }

          if (this.dataTierCard.tier) {
            this.selectedTier = this.dataTierCard.tier.name;
            this.getListTierActive();
            //Parse background color and assign it to new variable
            this.dataTierCard.tier.backgroundColorParsed = JSON.parse(
              this.dataTierCard.tier.backgroundColor
            );
          }
        }
      },
      error: () => {
        this.loadingTiercard = false;
      }
    });
  }

  goToMyVoucher() {
    this.router.navigate(['/profile/my-voucher'], {});
  }

  handleCheck(event: boolean) {
    this.isChecked = event;
  }
  onChangeTab(event: any) {
    const selectedTabName = event.activeItem.label.toLowerCase();
    let dataTier = this.dataListTierActive.filter(
      (item) => item.name === selectedTabName
    );

    dataTier = dataTier.map((item) => {
      const currentPosition = this.currentPosition;
      const isLockedTier = this.position > this.currentPosition ? true : false;
      return {
        ...item,
        currentPosition,
        isLockedTier,
        bgColor: this.backgroundColor
      };
    });

    this.dataTierService.setDataTier(dataTier[0]);
  }

  selectSegment(selectedTier: TierType) {
    this.gaService.addEvent(
      'membership_detail_tab_' + selectedTier.name,
      'membership',
      ''
    );
    this.windowActionService.dispatchEvent('tracker', {
      name: 'membership_detail_tab_' + selectedTier.name,
      category: 'membership'
    });
    this.dataListTierActive.forEach((item) => {
      item.isActive = item.name === selectedTier.name;
    });
    this.selectedTier = selectedTier.name;
    const selectedTierItem = this.dataListTierActive.find(
      (item) => selectedTier.name === item.name
    );

    this.selectedTierData = selectedTierItem;
  }
}
